:root {
    font-size: 62.5%;
    background-color: #F7F7F7;
    font-family: 'Roboto', 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
}

.wrap {
    padding-top: 2rem;
    max-width: 64rem;
    margin: 0 auto;
}

.jinmu-logo {
    @include site-shadow;
    width: 4.8rem;
    height: 4.8rem;
    border-radius: 50%;
    background-color: map-get($colors, main);
    margin: 0 auto 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    path {
        fill: #fff;
    }
}

.section-block {
    position: relative;
    padding: 0 3.2rem;
    margin-bottom: 4rem;
    &--no-padding {
        padding-left: 0;
        padding-right: 0;
    }
}

.book {
    width: 24rem;
    position: absolute;
    bottom: -4.5rem;
    left: 50%;
    transform: translate3d(-50%,0,0);
    img {
        max-width: 100%;
        display: block;
        margin: 0;
    }
}

.book-cover {
    @include site-shadow;
    border-radius: $site-border-radius;
    width: 100%;
    height: 38rem;
    font-family: 'Noto Serif TC', serif;
    font-weight: 900;
    text-align: center;
    box-sizing: border-box;
    padding: 2rem 2rem 0;
    font-size: 2.2rem;
    color: map-get($colors, darkGray);
    overflow: hidden;
    position: relative;
    &--page {
        height: 33rem;
    }
    &__wave {
        background: transparent url(../images/wave.svg) repeat-x center top;
        width: 6400px;
        height: 198px;
        position: absolute;
        left: 0;
        bottom: -3rem;
        animation-name: wave;
        animation-duration: 10s;
        animation-iteration-count: infinite;
        animation-timing-function: cubic-bezier( 0.36, 0.45, 0.63, 0.53);
        transform: translate3d(0, 0, 0);
        &:nth-of-type(2) {
            bottom: -3.5rem;
            animation-delay: -1s;
            opacity: 1;
        }
        &:nth-of-type(3) {
            bottom: -4rem;
            animation-delay: -2s;
            opacity: 0.75;
        }
        &:nth-of-type(4) {
            bottom: -4.5rem;
            animation-delay: -3s;
            opacity: 0.5;
        }
        &:nth-of-type(5) {
            bottom: -5rem;
            animation-delay: -4s;
            opacity: 0.25;
        }
    }
    p {
        line-height: 1.6;
        &:nth-child(1) {
            padding-bottom: 0.4rem;
            position: relative;
            margin-bottom: 0.4rem;
            &::after {
                width: 2rem;
                height: 1px;
                position: absolute;
                left: 50%;
                transform: translate3d(-50%,0,0);
                content: "";
                background-color: map-get($colors, remind);
                bottom: 0;
            }
        }
    }
    h1 {
        font-size: 2rem;
        line-height: 1.2;
        span {
            font-size: 2.8rem;
            display: block;
            margin-bottom: 0.6rem;
        }
    }
}

.about {
    &__slide {
        padding-bottom: 6rem;
        position: relative;
        .swiper-pagination {
            width: 100%;
            padding-top: 1.6rem;
        }
        .swiper-pagination-bullet {
            margin: 0 0.4rem;
            background-color: map-get($colors, darkGray);
            opacity: 1;
        }
        .swiper-pagination-bullet-active {
            background-color: map-get($colors, main);
        }
    }
    &__slider {
        @include site-shadow;
        border-radius: $site-border-radius;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
}

.feature {
    @include site-shadow;
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    border-radius: $site-border-radius;
    padding: 2rem 0;
    position: relative;
    &::before {
        content: "";
        width: 100%;
        height: 1px;
        background-color: map-get($colors, light);
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate3d(0,-50%,0);
    }
    &::after {
        content: "";
        width: 1px;
        height: 100%;
        background-color: map-get($colors, light);
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate3d(-50%,0,0);
    }
    &__item {
        width: 50%;
        margin-bottom: 2rem;
        padding-top: 2rem;
        svg {
            display: block;
            margin: 0 auto 1.5rem;
            path {
                fill: map-get($colors, main);
            }
        }
        p {
            text-align: center;
            font-size: 1.4rem;
            color: map-get($colors, main);
        }
    }
}

.course {
    &__slider {
        margin-bottom: 1rem;
        padding: 2rem;
    }
    .site-btn {
        margin: 0 3.2rem;
    }
    .swiper-slide {
        @include site-shadow;
        border-radius: $site-border-radius;
        overflow: hidden;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
}

.site-footer {
    background-color: map-get($colors, dark);
    padding: 1.5rem 0;
    &__slogan {
        font-size: 1.4rem;
        color: #fff;
        text-align: center;
        line-height: 1.6;
    }
    &__copyright {
        font-size: 1rem;
        color: #fff;
        text-align: center;
        line-height: 1.6;
    }
}

.remind {
    @include site-shadow;
    border-radius: $site-border-radius;
    padding: 2rem;
    background-color: map-get($colors, remind);
    margin-bottom: 2rem;
    svg {
        display: block;
        margin: 0 auto 1rem;
    }
    p {
        font-size: 1.2rem;
        text-align: center;
        line-height: 1.6;
        color: #fff;
    }
}

.general-user {
    &__ul-list {
        margin-bottom: 2rem;
        li {
            font-size:  1.2rem;
            line-height: 1.6;
            color: map-get($colors, gary);
            padding-left: 1.5rem;
            position: relative;
            &::before {
                width: 0.2rem;
                height: 0.2rem;
                background-color: map-get($colors, gary);
                position: absolute;
                content: "";
                left: 0.5rem;
                top: 0.8rem;
            }
        }
    }
    &__row {
        margin-bottom: 2rem;
        label {
            font-size: 1.2rem;
            display: block;
            color: map-get($colors, gray);
            padding-left: 0.6rem;
            border-left: 2px solid map-get($colors, gray);
            margin-bottom: 0.6rem;
        }
        input {
            @include site-shadow;
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
            box-shadow: inset 0;
            border: solid 2px #fff;
            border-radius: 0.8rem;
            display: block;
            width: 100%;
            box-sizing: border-box;
            line-height: 4rem;
            font-size: 1.4rem;
            color: map-get($colors, main);
            padding: 0 1rem;
            transition: all 0.4s ease-in-out;
            &:focus {
                border-color: map-get($colors, main);
            }
            &::-webkit-input-placeholder {
                color: map-get($colors, lightGary);
            }
            &:-ms-input-placeholder {
                color: map-get($colors, lightGary);
            }
            &::placeholder {
                color: map-get($colors, lightGary);
            }
        }
        button {
            width: 100%;
        }
    }
}

.unlimited-user {
    &__pd-info {
        padding-top: 2rem;
        border-top: dotted 1px map-get($colors, main);
        h3 {
            font-size: 2rem;
            color: map-get($colors, main);
            text-align: center;
            margin-bottom: 1.5rem;
        }
    }
    &__price {
        text-align: center;
        margin-bottom: 2rem;
    }
    &__price-title {
        vertical-align: middle;
        display: inline-block;
        padding: 0.5rem 1rem;
        background-color: map-get($colors, darkGray);
        border-radius: 2rem;
        color: #fff;
        font-size: 1.2rem;
        margin-right: 1rem;
    }
    &__old-price {
        vertical-align: middle;
        display: inline-block;
        color: map-get($colors, lightGary);
        font-size: 2.2rem;
        position: relative;
        &::after {
            width: 100%;
            height: 0.1rem;
            background-color: map-get($colors, lightGary);
            position: absolute;
            top: 50%;
            transform: translate3d(0,-50%,0);
            content: "";
            left: 0;
        }
    }
    &__new-price {
        vertical-align: middle;
        display: inline-block;
        color: map-get($colors, remind);
        font-size: 2.2rem;
    }
    &__svg-arrow {
        vertical-align: middle;
        display: inline-block;
    }
    &__slide {
        padding-bottom: 6rem;
        position: relative;
        .swiper-pagination {
            width: 100%;
            padding-top: 1.6rem;
        }
        .swiper-pagination-bullet {
            margin: 0 0.4rem;
            background-color: map-get($colors, darkGray);
            opacity: 1;
        }
        .swiper-pagination-bullet-active {
            background-color: map-get($colors, main);
        }
    }
    &__slider {
        @include site-shadow;
        border-radius: $site-border-radius;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__feature {
        @include site-shadow;
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        border-radius: $site-border-radius;
        position: relative;
        margin-bottom: 2rem;
    }
    &__item {
        padding: 2rem 0;
        flex: 1;
        &:nth-child(1),
        &:nth-child(2) {
            position: relative;
            &::after {
                width: 1px;
                height: 100%;
                position: absolute;
                content: "";
                background-color: map-get($colors, light);
                right: 0;
                top: 0;
            }
        }
        svg {
            display: block;
            margin: 0 auto 1rem;
            path {
                fill: map-get($colors, main);
            }
        }
        p {
            text-align: center;
            font-size: 1.2rem;
            color: map-get($colors, main);
        }
    }
}

.contact {
    padding-top: 2rem;
    text-align: center;
    font-size: 0;
    h4 {
        color: map-get($colors, darkGray);
        font-size: 1.4rem;
        margin-bottom: 2rem;
        line-height: 1.6;
    }
    &__btn-contact {
        @include site-shadow;
        width: 3.2rem;
        height: 3.2rem;
        background-color: map-get($colors, main);
        display: inline-block;
        line-height: 3.2rem;
        text-align: center;
        border-radius: 50%;
        position: relative;
        margin: 0 0.8rem;
        svg {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            path {
                fill: #fff;
            }
        }
    }
}

.buy-complete {
    &__pd-info {
        h3 {
            font-size: 2.2rem;
            margin-bottom: 1rem;
            text-align: center;
            color: map-get($colors, main);
        }
        h4 {
            font-size: 1.6rem;
            margin-bottom: 1rem;
            text-align: center;
            color: map-get($colors, main);
        }
    }
}

.hide {
    width: 30rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-45%,0);
    pointer-events: none;
    &__img {
        max-width: 100%;
        display: block;
        margin: 0;
        opacity: 0;
    }
}