$colors: (
    main: #00B4A4,
    remind: #F3467C,
    light: #eee,
    lightGary: #999,
    gary: #666,
    darkGray: #333,
    dark: #111
);

$site-border-radius: 1.2rem;

@mixin site-shadow {
    box-shadow: 0 1rem 3rem rgba(0,0,0,0.1),0 0.3rem 1rem rgba(0,0,0,0.1);
    //box-shadow: 0 20px 50px rgba(240, 46, 170, 0.7);
}

.site-text {
    font-size: 1.6rem;
    color: map-get($colors, gary);
    line-height: 1.6;
    text-align: center;
    margin-bottom: 1.2rem;
    font-weight: 700;
    strong {
        color: map-get($colors, main);
    }
}

.site-text-small {
    font-size: 1.2rem;
    color: map-get($colors, gary);
    line-height: 1.6;
    text-align: center;
    margin-bottom: 1.2rem;
    font-weight: 700;
    strong {
        color: map-get($colors, main);
    }
}

.site-title {
    text-align: center;
    margin-bottom: 1.2rem;
    font-weight: 700;
    text-align: center;
    span {
        display: inline-block;
        font-size: 2.2rem;
        border: solid 0.4rem map-get($colors, main);
        color: map-get($colors, main);
        line-height: 4rem;
        height: 4rem;
        padding: 0 2rem;
        border-radius: 4rem;
    }
}

.site-title-h3 {
    font-size: 1.6rem;
    color: map-get($colors, main);
    text-align: center;
    line-height: 1.6;
    margin-bottom: 0.8rem;
}

.site-btn {
    @include site-shadow;
    border-radius: $site-border-radius;
    display: block;
    border: 0;
    background: map-get($colors, main);
    background: linear-gradient(3deg, map-get($colors, main) 0%, lighten( map-get($colors, main),5% ) 100%);
    height: 6.4rem;
    font-size: 1.6rem;
    color: #fff;
    font-weight: 900;
    text-align: center;
    line-height: 6.4rem;
    padding: 0;
    position: relative;
    margin-bottom: 1.2rem;
    text-shadow: 0 0.1rem 0 darken(map-get($colors, main), 3%);
    &::after {
        content: "";
        width: 0.8rem;
        height: 0.8rem;
        border-right: solid 2px #fff;
        border-top: solid 2px #fff;
        position: absolute;
        right: 2rem;
        top: 50%;
        transform: translate3d(0,-50%,0) rotate(45deg);
    }
}

.site-icon {
    @include site-shadow;
    background-color: #fff;
    border-radius: 50%;
    width: 18rem;
    height: 18rem;
    display: block;
    margin: 0 auto 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    svg {
        path {
            fill: map-get($colors, main);
        }
    }
}